export const HUD_STYLE_ID = 'hud-style';
export const HUD_CSS = `
                /* 기본: 패널 위치 */
                #hud-info-panel {
                    position: absolute;
                    top: 8px;
                    left: 8px;
                    z-index: 1;
                    font-size: 11px;
                    color: #e6edf3;
                }

                #hud-info-panel .hud-info-body {
                    display: block;
                }

                /* camerafree 도움말 */
                .free-help { position:absolute; left:50%; transform:translateX(-50%); bottom:64px; padding:10px 12px; border-radius:10px; background:rgba(17,22,28,.95); color:#e6edf3; border:1px solid rgba(255,255,255,.08); box-shadow:0 10px 24px rgba(0,0,0,.45); z-index:5000; max-width:calc(100vw - 32px); font:11px system-ui,-apple-system,Segoe UI,Roboto,"Noto Sans KR",sans-serif; }
                .free-help h4 { margin:0 0 6px 0; font-size:14px; font-weight:700; color:#9ecbff; }
                .free-help .row{ display:flex; gap:10px; margin:4px 0; }
                .free-help kbd{ min-width:40px; padding:2px 6px; border-radius:6px; border:1px solid rgba(255,255,255,.12); background:rgba(255,255,255,.06); color:#fff; font:600 11px ui-monospace,SFMono-Regular,Menlo,Consolas,monospace; text-align:center; }
                
                #btn_cameraFreeMode.is-active {
                    background:#ff7c1c !important;
                    border-color:#ffd38a !important;
                    color:#fff;
                    position:relative;
                    box-shadow:
                        0 0 0 1px rgba(255,180,80,.45),
                        0 0 16px rgba(255,140,40,.75);
                    filter: brightness(1.03);
                    animation: cameraFreeGlow 1.4s ease-in-out infinite;
                }

                @keyframes cameraFreeGlow {
                    0% {
                        box-shadow:
                            0 0 0 0 rgba(255,180,80,.25),
                            0 0 10px rgba(255,140,40,.45);
                        filter: brightness(1.0);
                    }
                    50% {
                        box-shadow:
                            0 0 0 2px rgba(255,230,120,.7),
                            0 0 24px rgba(255,160,40,.95);
                        filter: brightness(1.12);
                    }
                    100% {
                        box-shadow:
                            0 0 0 0 rgba(255,180,80,.25),
                            0 0 10px rgba(255,140,40,.45);
                        filter: brightness(1.0);
                    }
                }

                #btn_orbitMode.is-active {
                    background:#ff7c1c !important;
                    border-color:#ffd38a !important;
                    color:#fff;
                    position:relative;
                    box-shadow:
                        0 0 0 1px rgba(255,180,80,.45),
                        0 0 16px rgba(255,140,40,.75);
                    filter: brightness(1.03);
                    animation: cameraFreeGlow 1.4s ease-in-out infinite;
                }

                /* Alpha 행 레이아웃 */
                .hud-alpha-row {
                    display:flex;
                    align-items:center;
                    gap:4px;
                    flex-wrap:wrap;
                }
                .hud-alpha-row span {
                    flex:0 0 auto;
                }
                .hud-alpha-value {
                    min-width:28px;
                    text-align:right;
                }
                .hud-alpha-range {
                    flex:1 1 auto;
                    min-width:80px;
                }

                /* 모델 옵션 드롭다운 */
                .hud-mode-wrap{
                    position:relative;
                }
                .hud-cg-menu{
                    position:absolute;
                    top:0;
                    left:100%;
                    margin-left:4px;
                    background:rgba(42,42,42,.7);
                    border-radius:8px;
                    padding:6px 8px;
                    border:1px solid rgba(255,255,255,.1);
                    box-shadow:0 8px 18px rgba(0,0,0,.45);
                    display:none;
                    flex-direction:column;
                    gap:4px;
                    min-width:120px;
                    z-index:6000;
                }
                .hud-mode-wrap.is-open .hud-cg-menu{
                    display:flex;
                }
                .hud-mode-wrap.is-open #hud-camera-group{
                    background:#4b4b4b !important;
                    color:#fff;
                }
                .hud-cg-item{
                    width:100%;
                    margin:0;
                    font-size:11px;
                    text-align:left;
                    white-space:nowrap;
                    border: 0px;
                    text-align: center;
                }
                .cesium-performanceDisplay-defaultContainer {
                    top: 85px !important;
                }

                /* 좁은 화면용 HUD */
                @media (max-width: 1024px) {
                    /* 카메라 영역 전체를 가로로 꽉 채우기 */
                    #hud-info-panel .hud-mode-wrap {
                        width: 100%;
                    }

                    #hud-info-panel {
                        display: flex;
                        flex-direction: column;
                        align-items: flex-start;
                        max-width: calc(100% - 16px);
                    }

                    /* 토글 버튼 노출 */
                    #hud-info-toggle {
                        display: inline-flex;
                        align-items: center;
                        justify-content: center;
                        padding: 3px 8px;
                        margin-bottom: 4px;
                        border-radius: 999px;
                        border: 1px solid rgba(255,255,255,0.4);
                        background: rgba(0,0,0,0.7);
                        color: #e6edf3;
                        font-size: 10px;
                        cursor: pointer;
                    }

                    /* 드롭다운 패널 기본: 접혀 있음 */
                    #hud-info-panel .hud-info-body {
                        position: relative;
                        margin-top: 4px;
                        border-radius: 8px;
                        padding: 6px 10px;
                        max-height: 0;
                        overflow: hidden;
                        opacity: 0;
                        transform-origin: top left;
                        transform: translateY(-4px);
                        transition:
                        max-height 0.18s ease,
                        opacity 0.18s ease,
                        transform 0.18s ease;
                    }

                    /* is-open 클래스일 때만 펼쳐지도록 */
                    #hud-info-panel.is-open .hud-info-body {
                        max-height: 400px;           /* 내용 높이보다 조금 크게 잡기 */
                        opacity: 1;
                        transform: translateY(0);
                    }

                    #hud-info-panel .hud-cg-menu {
                        position: static;      /* absolute + left:100% 제거 효과 */
                        margin-left: 0;
                        margin-top: 4px;

                        width: 100%;
                        box-shadow: 0 6px 14px rgba(0,0,0,0.5);
                    }
                }

                /* 큰 화면용 HUD */
                @media (min-width: 1025px) {
                    /* 토글 버튼은 안 보이게 */
                    #hud-info-toggle {
                        display: none;
                    }

                    /* 큰 화면에서는 항상 body 보이도록 강제 */
                    #hud-info-panel .hud-info-body {
                        display: block !important;
                        position: static;
                        max-height: none;
                        opacity: 1;
                        transform: none;
                    }
                }
`;

